.header__container {
  padding: 2rem 0rem;
  display: flex;
  align-items: center;
  gap: 5rem;
}
.header__foto, .header img {
  border-radius: 50%;
}
.header__foto {
  width: 250px;
}
.header__data {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.header__nombre h1 {
  font-size: 3rem;
  margin: 1rem 0;
  color: rgb(19, 19, 19);
}
.header__profesion h3 {
  color: rgb(61, 61, 61);
  font-weight: 500;
  margin: 0.5rem 0;
}
.header__localidad h4 {
  margin: 0;
  color: rgb(61, 61, 61);
  font-size: 1.1rem;
}

.navegacion {
  position: fixed;
  background-color: white;
  z-index: 10;
  right: 0;
  top: 0;
  height: 100vh;
  padding: 4rem 4rem;
  -webkit-box-shadow: 0px 0px 7px 3px #E1E1E1;
  box-shadow: 0px 0px 7px 3px #E1E1E1;
}
.navegacion__enlaces {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}
.navegacion__enlaces a {
  text-decoration: none;
  color: rgb(61, 61, 61);
  font-size: 1.1rem;
  background-color: transparent;
  border: 0px solid transparent;
}
.navegacion__dark {
  display: flex;
  align-items: center;
  gap: 1.5rem;
}

.switch-button {
  display: inline-block;
}

.switch-button .switch-button__checkbox {
  display: none;
}

.switch-button .switch-button__label {
  background-color: #f2f2f2;
  width: 3rem;
  height: 1.5rem;
  border-radius: 3rem;
  display: inline-block;
  position: relative;
}

.switch-button .switch-button__label:before {
  transition: 0.2s;
  display: block;
  position: absolute;
  width: 1.5rem;
  height: 1.5rem;
  background-color: white;
  content: "";
  border-radius: 50%;
  box-shadow: inset 0px 0px 0px 1px rgb(19, 19, 19);
}

.switch-button .switch-button__checkbox:checked + .switch-button__label {
  background-color: rgb(19, 19, 19);
}

.switch-button .switch-button__checkbox:checked + .switch-button__label:before {
  transform: translateX(1.5rem);
}

.secciones {
  padding: 3rem 0;
}
.secciones__enlaces a {
  text-decoration: none;
  color: rgb(19, 19, 19);
  font-size: 1rem;
  background-color: transparent;
  border: 0px solid transparent;
  margin-right: 3rem;
}
.secciones__enlaces {
  padding: 0 5rem;
}
.secciones__enlaces a:last-child {
  margin: 0;
}

.slides-container {
  overflow: hidden;
}
.slides-container__slide {
  display: flex;
  white-space: wrap;
  margin-block: 3rem;
}
.slides-container__slide-img {
  position: relative;
  padding: 1.5rem;
  height: 76px;
  width: 76px;
  margin-right: 3rem;
  background-color: white;
  border-radius: 50%;
}

.seccion {
  max-height: 1000px;
}
@media (min-width: 768px) {
  .seccion {
    max-height: 700px;
  }
}
@media (min-width: 1366px) {
  .seccion {
    max-height: 1000px;
  }
}
.seccion__titulo {
  font-size: 1.4rem;
  color: rgb(19, 19, 19);
}
.seccion__row {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.seccion__row-subdata {
  color: #535353;
  font-weight: 600;
}
.seccion__texto {
  text-align: justify;
  width: 90%;
}
.seccion__texto--proyectos {
  text-align: left;
}
.seccion__texto h3 {
  margin-top: 1.7rem;
  font-size: 1.4rem;
  margin-bottom: 0;
}
.seccion-titulos {
  overflow-y: hidden;
  max-height: 90%;
}
.seccion__card {
  margin-top: 5rem;
  background-color: #fafafa;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.seccion__card--proyectos {
  background-color: #f2f2f2;
  border-radius: 20px;
  transition: transform 0.2s ease;
  cursor: pointer;
}
.seccion__card--proyectos:hover {
  transform: scale(1.025);
}
.seccion__card-img {
  width: 400px;
}
.seccion__card-img--proyectos {
  max-height: 200px;
}
.seccion__card-img--proyectos img {
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
}
.seccion__card-img img {
  height: 100%;
  object-fit: cover;
}
.seccion__grid {
  padding-inline: 5rem;
}
.seccion__parrafo {
  line-height: 2rem;
  margin-bottom: 3rem;
  overflow: hidden;
  transition: height 0.4s ease;
}
.seccion__parrafo--proyectos {
  height: 200px;
}
.seccion__parrafo--expandido {
  height: 500px;
}
.seccion__guia {
  padding-block: 2rem;
  color: #4d4d4d;
}
.seccion__guia:hover {
  cursor: pointer;
}
.seccion.tecnologias {
  max-width: 100%;
}
.seccion__mark {
  padding: 0.2rem;
  border-radius: 50px;
}
.seccion__mark--celeste {
  background-color: rgb(115, 176, 255);
  color: white;
  font-weight: 700;
}